/* Contenedor general */
.container {
    margin: 20px;
    text-align: center;
}

/* T√≠tulo centrado */
.title {
    margin-bottom: 10px;
}

/* Tabla centrada con 90% del ancho */
.table {
    width: 90%;
    margin: 0 auto;
    border-collapse: collapse;
}

/* Celdas de tabla con 25% del ancho y estilos b√°sicos */
.table th,
.table td {
    padding: 10px;
    border: 1px solid #ddd;
    width: 25%;
}

/* Bot√≥n estilo b√°sico */
.button {
    margin: 15px 25px 30px 5px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 5px 15px;
    font-size: 16px;
    font-weight: bold;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    background-color: #ff5aa4;
    color: white;
    transition: background-color 0.3s;
}

.button:hover {
    background-color: #ff96c5; /* Color de fondo al pasar el cursor */
}

/* Contenedor de filtro y b√∫squeda con m√°s espacio encima */
.filter-search-container {
    display: flex;
    justify-content: space-between;
    align-items: center; /* Alineaci√≥n vertical centrada */
    margin: 20px 0;  /* M√°s espacio vertical */
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}

/* Ordenar alineado a la izquierda */
.filter-container {
    text-align: left;
    flex: 1;
}

.filter-container select {
    margin-right: 20px; /* A√±ade espacio entre los desplegables */
}

/* Barra de b√∫squeda alineada a la derecha */
.search-container {
    text-align: right;
    flex: 1;
    display: flex;
    justify-content: flex-end; /* Alineaci√≥n a la derecha */
}

/* Inputs y select b√°sicos con margen */
select {
    padding: 5px;
    font-size: 14px;
    height: 38px;  /* Ajuste de altura para alinearse con el contenido */
}

.search-container input[type="text"] {
    width: 250px;  /* Cambia este valor para ajustar el ancho a tu preferencia */
    padding: 5px;
    font-size: 14px;
    margin-left: 10px;
    height: 38px;  /* Asegura que la altura se mantenga consistente */
}