/* Contenedor principal */
.nominas-container {
    margin: 20px;
    text-align: center;
}

/* T√≠tulo principal */
.nominas-title {
    margin-bottom: 10px;
    text-align: center;
    font-weight: bold;
}

/* Contenedor de cada mes */
.mes-container {
    display: flex;
    flex-direction: column; /* Apila los elementos verticalmente */
    border-bottom: 1px solid #ddd; /* L√≠nea separadora */
    padding: 10px 0;
    font-size: 15px;
    color: #444;
    cursor: pointer; /* Indica que la fila es interactiva */
    transition: background-color 0.2s;
}

/* Cabecera del mes: Nombre a la izquierda, icono a la derecha */
.mes-header {
    display: flex;
    justify-content: space-between; /* Nombre del mes a la izquierda, icono a la derecha */
    align-items: center; /* Centra verticalmente */
    padding: 0 10px; /* Espaciado horizontal */
    font-size: 18px;
    font-weight: bold;
}

/* Icono de la cabecera */
.icono-check {
    font-size: 25px; /* Define un tama√±o fijo para ambos iconos */
    color: #D02B57;
    cursor: pointer;
    transition: transform 0.2s ease, color 0.2s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px; /* Ancho fijo */
    height: 30px; /* Alto fijo */
}

.icono-check:hover {
    transform: scale(1.1); /* Peque√±o efecto al pasar el rat√≥n */
}

/* Estilo para el mes activo */
.mes-container.activo h2 {
    color: black; /* Cambia el color del texto del mes activo */
}

/* Icono del mes activo */
.mes-container.activo .icono-check {
    font-size: 20px;
    color: #007BFF;
}

/* Contenido desplegable para las n√≥minas */
.nominas-detalle {
    margin-top: 10px;
    font-size: 14px;
    color: #666;
    width: 100%;
    display: flex;
    flex-direction: column; /* Tablas una debajo de otra */
    align-items: center; /* Centra las tablas horizontalmente */
}

/* Tabla de N√≥minas */
.tabla-nominas {
    width: 90%; /* Ajustar para centrar la tabla */
    border-collapse: collapse;
    margin-top: 10px;
    margin-bottom: 30px; /* A√±ade espacio entre tablas */
    font-size: 14px;
    text-align: center;
}

.tabla-titulo {
    text-align: center;
    font-weight: bold;
    font-size: 18px;
    padding: 10px 0;
    color: #000000;
    background-color: #f9f9f9; /* Color de fondo para destacarlo */
    border-bottom: 2px solid #ddd; /* L√≠nea separadora con las celdas */
}

.tabla-nominas th, .tabla-nominas td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
}

.tabla-nominas th {
    background-color: #f4f4f4;
    font-weight: bold;
}

.tabla-nominas tbody tr:nth-child(even) {
    background-color: #f9f9f9;
}

.tabla-nominas tbody tr:hover {
    background-color: #f1f1f1;
}

.tabla-footer {
    text-align: right; /* Alinea todo el contenido a la derecha */
    padding-right: 15px; /* Espacio adicional a la derecha */
}

.texto-a-cobrar {
    display: inline-block; /* Permite ajustes finos de alineaci√≥n */
    width: 100%; /* Asegura que ocupe todo el ancho disponible */
    text-align: right; /* Alinea el texto dentro del contenedor */
    font-weight: bold; /* Resalta el texto */
    color:#000000;
}

.tabla-total {
    color:#000000;
    font-weight: bold;
}
